@import "variables.scss";
@import "window_style.scss";
@import "boards_style.scss";
@import "header_style.scss";


* {box-sizing: border-box;
    padding: 0;
    margin: 0;
    outline: none;
    border: none;
    background-color: inherit;
    text-transform: none;
    text-decoration: none;
    color: inherit;
}

html, body {
max-height: 100vh;
height: 100%;
}

@media all and (orientation:landscape) {
    html, body {
        max-height: none;
        height: 100%;
        }
}

body {
    display: flex;
    flex-direction: column; 
    justify-content: space-between;
}

main {
    display: flex;
    flex: 1;
    flex-direction: column; 
    overflow: hidden;          
}

a, span, p, input, button, date, select, option, textarea, ::placeholder {
    font-family: 'Oswald', sans-serif;
    font-size: clamp(0.9rem, 1.6vw, 1.2rem);
    line-height: 1.2;
    color:$font-color-board;
}

.wrapper{    
    display: flex;    
    justify-content: center;
    align-items: flex-start; 
    background-color: #575753; 
    min-height: 100%;
    min-width: 15rem;
    overflow: auto;    
  }

.container {
    max-width: 1416px;     
    flex: 1;    
    padding: 0.5rem;
    height: 100%;     
    overflow: hidden;
}

.header {   
    display: flex;
    justify-content: space-between; 
    align-items: center;
    border: 1px solid #fff;
    border-radius: 1rem;
    padding-top: clamp(2rem, 5vw, 4rem);
    padding-bottom: clamp(2rem, 5vw, 4rem);
    padding-left: clamp(1rem, 2.5vw, 2rem);
    padding-right: clamp(1rem, 2.5vw, 2rem);    
    background: linear-gradient(90deg, #d44d34,#af31ab,#6cd5c0); 
  
    &__text {
        text-transform: uppercase; 
        color: #fff; 
        font-size: clamp(1.5rem, 3vw, 2.5rem);    
    }

    &__time {
        border: 1px solid #000;
        border-radius: 0.5rem;  
        padding: clamp(0.5rem, 1.25vw, 1rem); 
        background-color: rgb(231, 221, 221); 
        color: rgb(30, 17, 146); 
        font-size: clamp(1rem, 2vw, 1.5rem);
    }
}

.btn {
    border: 1px solid rgb(241, 234, 234);
    padding: clamp(0.25rem, 0.5vw, 0.5rem);
    border-radius: 0.3rem; 
    color:rgb(241, 233, 233);  
    background-color: #a65dcf;                                  
}

.btn:hover {                    
    background-color: #763999; 
    border: 1px solid #673485;                                                    
}

.btn:active {
    border: 1px solid rgb(241, 234, 234);  
}          

date {
display: block;
text-align: end;
}



@media (max-width:960px) {
    @media screen and (orientation: landscape) {
         html, main {    
           height: auto;           
        }     
        
        .header {            
            padding-top: clamp(1rem, 2.5vw, 2rem);
            padding-bottom: clamp(1rem, 2.5vw, 2rem);                 
        }

            .board__task-wrapper {
            min-height: 0px; 
          }
}}

@media (max-width:676px) {

    @media screen and (orientation: landscape) {
        .board-wrapper {    
            align-items: flex-start;   
        }
    
        .board{
            height: 100%;
            align-items:stretch;            
            width: 100%;    
        }    
    }
       .board-wrapper {    
        flex-direction: column; 
        max-width: 25rem;
        max-height: 100%;  
        overflow: hidden;

        .board {       
           align-items: stretch; 
           max-height: 50%;       
           height: 100%;
           
            .board__header {                    
            padding-right: 3rem;                                     
            }

            .board__header::after {                    
            content: url('../image/arrow.svg');  
            height: 1.5rem;
            width: 1.2rem;   
            position: absolute;
            right: 0.5rem; 
            top: 50%;
            transform: translateY(-60%) rotate(180deg);   
            }

           .board__task-wrapper{ 
            opacity: 1;                    
            transition: all 0.2s ease-in-out;
            }   

            .board__footer{ 
                opacity: 1;      
                transition: all 0.2s ease-in-out;
            }
        }

            .task-hidden {    
                 min-height: auto;
                 border: none;                 
                 
                 .board__header {                    
                     border-bottom: 2px solid #fff;                                       
                 }

                 .board__header::after {                    
                   transform: translateY(-40%) rotate(0deg); 
                }

                .board__header::after:active {                    
                    content: url('../image/arrow_hover.svg');
                }
                     
                .board__task-wrapper{ 
                    max-height: 0px;  
                    min-height: auto;                 
                    opacity: 0%;    
                    transition: all 0.2s ease-in-out;
                }   
                              
                .board__footer{ 
                    max-height: 0px;                                       
                    opacity: 0%;  
                    transition: all 0.2s ease-in-out;
                }
            }        
    }   
}

.task_in-progress {
    background: $background-color-inprogress;
}
    
.task_done {
    background: $background-color-done;
} 

.board__task-wrapper::-webkit-scrollbar, select::-webkit-scrollbar {
    width: clamp(1.2rem, 2vw, 1.5rem); /* ширина скролла */    
    background-color: #af3176;
}

/* ползунок скроллбара */
.board__task-wrapper::-webkit-scrollbar-thumb, select::-webkit-scrollbar-thumb {
    background-color: #7c24af;
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #faf3f5;
}

.board__task-wrapper::-webkit-scrollbar-thumb:hover, select::-webkit-scrollbar-thumb:hover {
    background-color: #841f9b;
    box-shadow: inset 1px 1px 5px #faf3f5;
}


